<template>
    <Modal :show="vShow" :title="title" @onClose="handleModalClose">
        <template v-slot:content>
            <div class="title-wrap d-flex justify-content-between mb-3">
                <div class="title-text font-weight-bold">Profile</div>
                <!-- <b-link class="text-primary text-underline font-weight-bold">Edit</b-link> -->
            </div>
            <div class="profile-item mb-4">
                <div class="profile-item-label text-secondary mb-2">Email</div>
                <div class="profile-item-text text-dark">{{ $auth.user.email }}</div>
            </div>
            <div class="title-wrap d-flex justify-content-between mb-3">
                <div class="title-text font-weight-bold">Membership</div>
                <b-link v-if="$auth.user.isFree" class="text-primary text-underline font-weight-bold"
                    :disabled="disabled.upgrade" @click="handleUpgrade"><b-spinner :hidden="!loading.upgrade"
                        small></b-spinner>Upgrade</b-link>
            </div>
            <div class="membership-wrap">
                <b-badge pill variant="info" class="py-3 px-4 membership-wrap-badge">{{ $auth.user.isFree ? 'Free Member' :
                    'Pro Member' }}</b-badge>
                <b-link v-if="!$auth.user.isFree" class="text-danger text-underline font-weight-bold ml-2">Cancel
                    Membership</b-link>
            </div>
            <div class="border-top my-3"></div>
            <div class="text-center">
                <b-button pill variant="outline-danger" size="lg" class="px-4" @click="handleLogout"><b-spinner
                        :hidden="true" small></b-spinner><i class="bi bi-box-arrow-right mr-2"></i>Logout</b-button>
            </div>
        </template>
    </Modal>
</template>
  
<script>
import Modal from '@/components/modal'
import { checkUserPayStatusAPI, getUserCheckoutAPI } from '@/api/user'

export default {
    name: 'Account',
    components: {
        Modal,
    },
    props: {
        show: {
            type: Boolean,
            default: false
        },
    },
    data() {
        return {
            vShow: false,
            title: 'My Account',
            isFree: true,
            loading: {
                upgrade: false
            },
            disabled: {
                upgrade: false
            },
        }
    },
    computed: {
        userId() {
            return this.$auth.user.sub && this.$auth.user.sub.replace('auth0|', '') || ''
        }
    },
    watch: {
        show: {
            handler(newVal, oldVal) {
                this.vShow = newVal
            }
        }
    },
    mounted() {
    },
    methods: {
        // 关闭事件
        handleModalClose() {
            this.$emit("onClose");
        },
        // 用户登出
        handleLogout() {
            this.$auth.logout({
                logoutParams: {
                    returnTo: window.location.origin
                }
            });
        },
        // 检查用户付费状态
        checkUserPayStatus() {
            let params = {
                userId: this.userId,
            }
            checkUserPayStatusAPI(params).then(data => {
                this.isFree = !data.is_paid
            }).catch(error => { })
        },
        // 订阅升级
        handleUpgrade() {
            this.loading.upgrade = true

            let params = {
                userId: this.userId,
            }

            getUserCheckoutAPI(params).then(url => {
                this.loading.upgrade = false
                window.location.href = url
            }).catch(error => {
                this.loading.upgrade = false
            })
        },
    }
}
</script>
  
<style scoped lang="scss">
.membership-wrap {
    &-badge {
        color: #6E9EAF;
        background-color: #CEEFFB;
    }
}
</style>



